window.dom = {
    create(string) {
        const container = document.createElement('template')
        //Âä†templateÊòØÂõ†‰∏∫ÊØîÂ¶ÇdivÈáåÈù¢‰∏çËÉΩÁõ¥Êé•Âä†tdÔºåËøùÂèçhtmlËØ≠Ê≥ï
        container.innerHTML = string.trim() // ÂéªÊéâÁ©∫Ê†º
        return container.content.firstChild  //Âõ†‰∏∫ÈÄöËøátemplate‰∏çËÉΩÁõ¥Êé•ÈÄöËøácontainer.childrenÊù•Ëé∑ÂèñÔºåË¶ÅÈÄöËøácontent.firstChild
    },
    //Âú®  #test ÂêéÈù¢Âä†‰∏Ä‰∏™node
    after(node, node2) {
        //ÊâæÂà∞nodeÁöÑÁà∏Áà∏ÔºåÁÑ∂ÂêéË∞ÉÁî®insertBeforeÁöÑÊñπÊ≥ïÔºåÁÑ∂ÂêéÊäänode2ÊèíÂà∞nodeÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπÁöÑÂâçÈù¢v
        node.parentNode.insertBefore(node2, node.nextSibling)
    },
    before(node, node2) {
        node.parentNode.insertBefore(node2, node)
    },
    append(parent, node) {
        parent.appendChild(node)
    },
    warp(node, parent) {
        dom.before(node, parent)
        dom.append(parent, node)
    },
    remove(node) {
        //Âà†Èô§‰∏Ä‰∏™node,ÊâæÂà∞Áà∏Áà∏ÔºåËÆ©Áà∏Áà∏Âà†ÊéâÂÑøÂ≠ê
        node.parentNode.removeChild(node)
        return node
    },
    empty(node) {
        const {childNodes} = node
        const array = []
        let x = node.firstChild
        while (x) {
            array.push(dom.remove(node.firstChild))
            x = node.firstChild //Âõ†‰∏∫Á¨¨‰∏Ä‰∏™ÂÑøÂ≠êÂ∑≤ÁªèË¢´ÁßªÈô§ÔºåÊâÄ‰ª•Áé∞Âú®Á¨¨‰∫å‰∏™ÂÑøÂ≠êÂèòÊàêÁ¨¨‰∏Ä‰∏™ÂÑøÂ≠ê
        }
        return array
    },
    attr(node, name, value) {   //ÈáçËΩΩ
        if (arguments.length === 3) {
            node.setAttribute(name, value)
        } else if (arguments.length === 2) {
            return node.getAttribute(name)
        }

    },
    text(node, string) {   // Ëøô‰∏™ÊñπÊ≥ïÂè´ÈÄÇÈÖç
        if (arguments.length === 2) {
            if ('innerText' in node) {
                node.innerText = string //ie
            } else {
                node.textContent = string // chrome
            }
        } else if (arguments.length === 1) {
            if ('innerText' in node) {
                return node.innerText
            } else {
                return node.textContent
            }
        }
    },
    html(node, string) {
        if (arguments.length === 2) {
            node.innerHTML = string
        } else {
            return node.innerHTML
        }
    },
    style(node, name, value) {
        if (arguments.length === 3) {
            //ÊØîÂ¶Ç‰ºöÊÉ≥ÂÜôdom.style(div, 'color', 'red')
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === 'string')
            // ÂèØËÉΩ‰ºöÊòØÊÉ≥Ëé∑Âèñ dom.style(div, 'color')
                return node.style[name]
        } else if (name instanceof Object) {
            // dom.style(div, {color: 'red']
            const object = name
            for (let key in object) {
                //key: border / color
                // node.style.color = ....
                // node.style.color = ....
                node.style[key] = object[key]
                // Â¶ÇÊûúÊòØ.style.keyÁöÑËØùÔºåkeyÊòØ‰∏Ä‰∏™Â≠óÁ¨¶‰∏≤Ôºå[key]ÊòØ‰∏Ä‰∏™Â≠óÁ¨¶‰∏≤
            }
        }
    },
    class: {
        add(node, className) {
            node.classList.add(className)
        },
        remove(node, className) {
            node.classList.remove(className)
        },
        has(node, className) {
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) {
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) {
        node.removeEventListener(eventName, fn)
    },
    find(selector, scope) {
        return (scope || document).querySelectorAll(selector)
        //Â¶ÇÊûúÊúâscopeÔºåÈÇ£Â∞±ÊòØscope.quer...Ê≤°Êúâscope,ÈÇ£Â∞±ÊòØdocument.....
        //È´òÊâãÂÜôÊ≥ï
    },
    parent(node) {
        return node.parentNode
    },
    children(node) {
        return node.children
    },
    siblings(node) {
        return Array.from(node.parentNode.children).filter(n => n !== node)
    },
    next(node) {
        let x = node.nextSibling
        while (x && x.nodeType === 3) {//1ÊòØnode, 3ÊòØtext ÁöÑÊÑèÊÄù
            x = x.nextSibling
        }
        return x
    },
    previous(node) {
        let x = node.previousSibling
        while (x && x.nodeType === 3) {//1ÊòØnode, 3ÊòØtext ÁöÑÊÑèÊÄù
            x = x.previousSibling
        }
        return x
    },
    each(nodeList, fn) {
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    index(node) {
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }

}

